<template>
	<div class="infoshow">
		<div>
			<el-form :inline="true" ref="search_data" :model='search_data'>
				<el-form-item label="投标时间筛选:">
					<el-date-picker v-model="search_data.startTime" type="datetime" placeholder="选择开始时间">
					</el-date-picker> &nbsp;&nbsp;&nbsp;
					<el-date-picker v-model="search_data.endTime" type="datetime" placeholder="选择结束时间">
					</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="small" icon="search" @click='onScreeoutMoney()'>筛选</el-button>
				</el-form-item>
				<el-form-item class="btnRight">
					<el-button type="primary" size="small" icon="view" @click='dialog.show=true'>新增部门</el-button>
				</el-form-item>
			</el-form>
		</div>
		<div class="table_container">
			<el-table :data='departTable' max-height="650" border :default-sort="{prop: 'date', order: 'descending'}" style="width: 100%">
				<el-table-column prop="departName" label="部门名称" align='center' sortable width="300">
				</el-table-column>
				<el-table-column prop="departDesc" label="部门描述" align='center'>
				</el-table-column>
				<el-table-column label="操作" align="center" width="250">
					<template slot-scope="scope">
						<el-button size="mini" type="success" @click="detailDep(departTable[scope.$index])">详情</el-button>
						<el-button size="mini" @click="updataDepart(departTable[scope.$index])" type="info">编辑</el-button>
						<el-button size="mini" type="danger" @click="deleteDepart(departTable[scope.$index])">删除</el-button>

					</template>
				</el-table-column>
			</el-table>
			<!-- 分页 -->
			<el-row>
				<el-col :span="24">
					<div class="pagination">
						<el-pagination v-if='paginations.total > 0' :page-sizes="paginations.page_sizes" :page-size="paginations.page_size"
						 :layout="paginations.layout" :total="paginations.total" :current-page.sync='paginations.page_index'
						 @current-change='handleCurrentChange' @size-change='handleSizeChange'>
						</el-pagination>
					</div>
				</el-col>
			</el-row>
		</div>

		<el-dialog :title="dialog.title" :visible.sync="dialog.show" :close-on-click-modal='false' :close-on-press-escape='false'
		 :modal-append-to-body="false">
			<el-form label-width="120px">
				<el-form-item label="部门名称">
					<el-input v-model="depDialog.departName" placeholder="请输入部门名称 " clearable></el-input>
				</el-form-item>
				<el-form-item label="部门描述">
					<el-input v-model="depDialog.departDesc" type="textarea" :rows="4" placeholder="请输入部门描述 " clearable></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="
					 info" @click="dialog.show=false">取 消</el-button>
					<el-button type="primary" @click="submitDep">提交</el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
		
		<el-dialog :title="updataDialog.title" :visible.sync="updataDialog.show" :close-on-click-modal='false'
		 :close-on-press-escape='false' :modal-append-to-body="false">
			<el-form label-width="120px">
				<el-form-item label="部门名称">
					<el-input v-model="upDetail.departName" placeholder="请输入部门名称 " clearable></el-input>
				</el-form-item>
				<el-form-item label="部门描述">
					<el-input v-model="upDetail.departDesc" type="textarea" :rows="4" placeholder="请输入部门描述 " clearable></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="
					 info" @click="updataDialog.show=false">取 消</el-button>
					<el-button type="primary" @click="submitUp">保存</el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
		<el-dialog :title="detailDialog.title" :visible.sync="detailDialog.show" :close-on-click-modal='false'
		 :close-on-press-escape='false' :modal-append-to-body="false">
			<el-form label-width="120px">
				<el-form-item label="部门名称">
					<el-input v-model="depDetail.departName" placeholder="请输入部门名称 " :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="部门描述">
					<el-input v-model="depDetail.departDesc" type="textarea" :rows="4" placeholder="请输入部门描述 " :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="部门成员">
					<span class="departUser" v-for="(o, index) in depDetail.users" :key="index">
						{{o.userName}}
					</span>
				</el-form-item>
				<el-form-item>
					<el-button type="
					 info" @click="detailDialog.show=false">关闭</el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
	</div>

</template>

<script>
	import qs from 'qs'
	export default {
		name: "department",
		inject: ['reload'],
		data() {
			return {
				departmentData: [{
					label: "湖北民生电子商务有限公司",
					children: []
				}],
				departTable: [],
				allTableData: [],
				filterTableData: [],
				//需要给分页组件传的信息
				paginations: {
					page_index: 1, // 当前位于哪页
					total: 0, // 总数
					page_size: 5, // 1页显示多少条
					page_sizes: [5, 10, 15, 20], //每页显示多少条
					layout: "total, sizes, prev, pager, next, jumper" // 翻页属性
				},
				search_data: {
					startTime: "",
					endTime: ""
				},
				
				dialog: {
					show: false,
					title: "新增部门",
					option: "add"
				},
				depDialog: {},
				updataDialog: {
					show: false,
					title: "修改部门信息",
					option: "updata"
				},
				upDetail: {},
				detailDialog:{
					show: false,
					title: "部门信息",
					option: "detail"
				},
				depDetail: {},

			}
		},
		created() {
			this.getDepartment();
		},
		methods: {
						getDepartment() {
							this.$axios.get('/department/findDepartments').then(res => {
								// console.log(JSON.stringify(res.data.data)+"部门数据");
								this.departTable = res.data.data;
								this.allTableData= res.data.data;
								this.filterTableData= res.data.data;
							});
			
						},
			
			
			
			//查看每个部门详细信息
			detailDep(item) {   //如果用id重新获取该部门详情，参数为id
				this.detailDialog.show = true;
// 					this.$axios({
// 						method: 'get',
// 						url: '/department/findDepartByDepartId',
// 						params: {
// 							departId:id
// 						},
// 					}).then(res => {
// 						this.depDetail = res.data.data;
// 						// console.log(res.data.data)
// 					});
               this.depDetail = item;
			},
			
			
			//删除单个部门
			deleteDepart(dep) {
				if (!dep.users.length) {
					this.$confirm('是否删除当前部门, 是否继续?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => { 

						this.$axios({
							method: 'get',
							url: '/department/deleteDepatById',
							params: {
								departId: dep.departId
							},
						}).then(res => {
							this.$message({
								type: 'success',
								message: '删除成功!'
							});
							this.reload();
						});

					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消删除'
						});
					});
				} else {
					this.$message({
						type: 'warning',
						message: '有员工隶属于该部门，不允许删除!'
					});
				}
			},
			
			
			//修改部门信息
			updataDepart(item) {
				this.upDetail = JSON.parse(JSON.stringify(item));
				this.updataDialog.show = true;
			},
			//提交部门信息
			submitUp(){
				var data = {
					departId: this.upDetail.departId,
					departName: this.upDetail.departName,
					departDesc: this.upDetail.departDesc
				}
				this.$axios({
					method: 'post',
					url: '/department/updateDapart',
					headers: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: qs.stringify(data)
				}).then(res => {
					this.$message({
						type: 'success',
						message: '部门信息修改成功！'
					});
					this.updataDialog.show = false;
					this.reload();
				});
				
			},
			//新建部门信息提交
			submitDep() {
				this.$axios({
					method: 'post',
					url: '/department/saveDepart',
					data: qs.stringify(this.depDialog)
				}).then(res => {
					this.$message({
						type: 'success',
						message: '新增部门成功！'
					});
					this.dialog.show = false;
					this.reload();
				});
			},
			handleCurrentChange(page) {
				// 当前页
				let sortnum = this.paginations.page_size * (page - 1);
				let table = this.allTableData.filter((item, index) => {
					return index >= sortnum;
				});
				// 设置默认分页数据
				this.tableData = table.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},
			handleSizeChange(page_size) {
				// 切换size
				this.paginations.page_index = 1;
				this.paginations.page_size = page_size;
				this.tableData = this.allTableData.filter((item, index) => {
					return index < page_size;
				});
			},
			setPaginations() {
				// 总页数
				this.paginations.total = this.allTableData.length;
				this.paginations.page_index = 1;
				this.paginations.page_size = 5;
				// 设置默认分页数据
				this.tableData = this.allTableData.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},

		},

	}
</script>

<style>
	.infoshow {
		width: 100%;
		height: 100%;
		padding: 16px;
		box-sizing: border-box;
	}

	/* 
	.fillcontain {
		width: 100%;
		height: 100%;
		padding: 16px;
		box-sizing: border-box;
	} */

	.btnRight {
		float: right;
	}

	.pagination {
		text-align: right;
		margin-top: 10px;
	}
	.departUser{
		margin-right: 10px;
		font-weight: 600;
	}
</style>



