<template>
	<div class="fillcontain">
		<div>
			<el-form :inline="true" ref="search_data" :model='search_data'>
				<el-form-item label="时间筛选:">
					<el-date-picker v-model="search_data.startTime" type="datetime" placeholder="选择开始时间">
					</el-date-picker> &nbsp;&nbsp;&nbsp;
					<el-date-picker v-model="search_data.endTime" type="datetime" placeholder="选择结束时间">
					</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="small" icon="search" @click='onScreeoutMoney()'>筛选</el-button>
				</el-form-item>
				<el-form-item class="btnRight">
					<el-upload class="upload-demo" action="" multiple :limit="5" :before-upload="beforeUpload" :on-exceed="handleExceed">
						<el-button size="small" type="primary">点击上传</el-button>
					</el-upload>
					<!-- <el-button type="primary" size="small" icon="view" @click='uploadFile()'>上传文件</el-button> -->
				</el-form-item>
			</el-form>
		</div>
		<div class="table_container">
			<el-table v-if="tableData" :data='tableData' max-height="650" border :default-sort="{prop: 'date', order: 'descending'}"
			 style="width: 100%">
				<el-table-column label="编号" align='center' width="250">
					<template slot-scope="scope">
						{{scope.$index+1}}
					</template>
				</el-table-column>
				<el-table-column prop="originName" label="文件名" align='center'>
				</el-table-column>
				<!-- <el-table-column prop="position" label="面试岗位" align='center' >
				</el-table-column> -->
				<el-table-column label="操作" align="center" width="250">
					<template slot-scope="scope">
						<el-button size="mini" @click="downloadFile(tableData[scope.$index])" type="info">下载</el-button>
						<el-button size="mini" type="danger" @click="deleteFile(tableData[scope.$index].resourceId)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
			<!-- 分页 -->
			<el-row>
				<el-col :span="24">
					<div class="pagination">
						<el-pagination v-if='paginations.total > 0' :page-sizes="paginations.page_sizes" :page-size="paginations.page_size"
						 :layout="paginations.layout" :total="paginations.total" :current-page.sync='paginations.page_index'
						 @current-change='handleCurrentChange' @size-change='handleSizeChange'>
						</el-pagination>
					</div>
				</el-col>
			</el-row>
		</div>

	</div>
</template>

<script>
	import qs from 'qs'
	export default {
		name: "publicFile",
		inject: ['reload'],
		data() {
			return {
				tableData: [],
				allTableData: [],
				filterTableData: [],
				search_data: {
					startTime: "",
					endTime: ""
				},
				//需要给分页组件传的信息
				paginations: {
					page_index: 1, // 当前位于哪页
					total: 0, // 总数
					page_size: 5, // 1页显示多少条
					page_sizes: [5, 10, 15, 20], //每页显示多少条
					layout: "total, sizes, prev, pager, next, jumper" // 翻页属性
				},
				userId: 0
			}
		},
		created() {
			this.userId = localStorage.getItem('userId');
			this.getAllFile();
		},
		methods: {
			getAllFile() {
				this.$axios({
					method: 'get',
					url: '/load/getPublicFile',
				}).then(res => {
					this.tableData = res.data.data;
					console.log(res.data);
				});
			},
			uploadFile(file) {
				//上传文件
				var fd = new FormData();
				fd.append('file', file); //传文件
				fd.append('isPublic', 1); //传其他参数
				fd.append('userId', this.userId);
				this.$axios({
					method: 'post',
					url: 'http://47.112.143.90:8080/dfs/upload',
					headers: {
						'Content-Type': 'multipart/form-data'
					},
					data: fd
				}).then(res => {
					this.$message({
						type: 'success',
						message: '文件上传成功！'
					});
					this.reload();
				})

			},
			downloadFile(item) {
				//下载文件
				var data = {
					resourceId: item.resourceId
				};
				this.$axios({
					method: 'post',
					url: 'http://47.112.143.90:8080/dfs/download',
					headers: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: qs.stringify(data),
					responseType: 'blob' // 表明返回服务器返回的数据类型
				}).then(res => {
					const content = res.data;
					const blob = new Blob([content])
					const fileName = item.originName
					if ('download' in document.createElement('a')) { // 非IE下载
						const elink = document.createElement('a')
						elink.download = fileName
						elink.style.display = 'none'
						elink.href = URL.createObjectURL(blob)
						document.body.appendChild(elink)
						elink.click()
						URL.revokeObjectURL(elink.href) // 释放URL 对象
						document.body.removeChild(elink)
					} else { // IE10+下载
						navigator.msSaveBlob(blob, fileName)
					}
				});
			},
			deleteFile(resourceId) {
				//删除文件
				this.$confirm('是否确认删除该文件, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {

					this.$axios({
						method: 'get',
						url: 'http://47.112.143.90:8080/dfs/delete',
						params: {
							resourceId: resourceId
						},
					}).then(res => {
						this.$message({
							type: 'success',
							message: '删除成功!'
						});
						this.reload();
					});

				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
			beforeUpload(file) {
				this.uploadFile(file);
			},
			handleExceed(files, fileList) {
				this.$message.warning("当前限制选择5个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件");
			},
			handleCurrentChange(page) {
				// 当前页
				let sortnum = this.paginations.page_size * (page - 1);
				let table = this.allTableData.filter((item, index) => {
					return index >= sortnum;
				});
				// 设置默认分页数据
				this.tableData = table.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},
			handleSizeChange(page_size) {
				// 切换size
				this.paginations.page_index = 1;
				this.paginations.page_size = page_size;
				this.tableData = this.allTableData.filter((item, index) => {
					return index < page_size;
				});
			},
			setPaginations() {
				// 总页数
				this.paginations.total = this.allTableData.length;
				this.paginations.page_index = 1;
				this.paginations.page_size = 5;
				// 设置默认分页数据
				this.tableData = this.allTableData.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},
			onScreeoutMoney() {
				// 筛选
				if (!this.search_data.startTime || !this.search_data.endTime) {
					this.$message({
						type: "warning",
						message: "请选择时间区间"
					});
					this.getProfile();
					return;
				}
				const stime = this.search_data.startTime.getTime();
				const etime = this.search_data.endTime.getTime();
				this.allTableData = this.filterTableData.filter(item => {
					let date = new Date(item.date);
					let time = date.getTime();
					return time >= stime && time <= etime;
				});
				// 分页数据
				this.setPaginations();
			}

		}
	}
</script>

<style scoped>
	.fillcontain {
		width: 100%;
		height: 100%;
		padding: 16px;
		box-sizing: border-box;
	}

	.btnRight {
		float: right;
	}

	.pagination {
		text-align: right;
		margin-top: 10px;
	}
</style>



