<template>
	<div class="voteDig">

		<!-- 未超时的投票 -->
		<el-dialog v-if="!voteDetail.overtime" :title="voteDetail.content" :visible.sync="votedialog.show"
		 :close-on-click-modal='false' :close-on-press-escape='false' :modal-append-to-body="false" width="40%">
			<div class="form" style="margin-left: 15px;width: 100%;" v-if="!voteDetail.isClose">
				<el-form v-if="voteDetail.isSelectOne">
					<el-radio-group v-model="checkedValue" class="el_radiogroup">
						<el-form-item class="el_form_item" v-for="(o, index) in voteDetail.voteOptionList" :key="index" :offset="index > 0 ? voteDetail.voteOptionList.length : 0">
							<el-radio class="el_radio" :label="o.optionId">{{o.content}}</el-radio>
						</el-form-item>
					</el-radio-group>
				</el-form>
				<el-form v-else>
					<el-checkbox-group v-model="checkedList" class="el_radiogroup">
						<el-form-item class="el_form_item" v-for="(o, index) in voteDetail.voteOptionList" :key="index" :offset="index > 0 ? voteDetail.voteOptionList.length : 0">
							<el-checkbox class="el_radio" :label="o.optionId">{{o.content}}</el-checkbox>
						</el-form-item>
					</el-checkbox-group>
				</el-form>
				<div class="bor_row">
					<el-row>
						<el-col :span="16">
							{{voteDetail.isSelectOne?"单选":"多选"}}
							<el-button type="text" @click="deleteVote(voteDetail.themeId)">删除投票</el-button>
						</el-col>
						<el-col :span="8">截止时间：{{voteDetail.endTime}}</el-col>
					</el-row>
				</div>
				<div class="buttonset">
					<el-button size="small" @click="votedialog.show=false">取 消</el-button>&nbsp;&nbsp;
					<el-button size="small" type="primary" @click='onSubmit()'>投票</el-button>
				</div>
			</div>
			<div v-else class="form" style="margin-left: 15px;width: 100%;">
				<el-row>
					<el-col :span="20">{{voteDetail.isSelectOne?"单选":"多选"}}</el-col>
					<el-col :span="4">已投{{voteNumber}}票</el-col>
				</el-row>
				<el-form class="el_form">
					<el-form-item style="width: 95%;" class="el_form_item" v-for="(o, index) in voteDetail.voteOptionList" :key="index"
					 :offset="index > 0 ? voteDetail.voteOptionList.length : 0">
						<el-row>
							<el-col :span="21">
								{{o.content}}
							</el-col>
							<el-col :span="3">{{o.userList.length}}票</el-col>
						</el-row>
					</el-form-item>
				</el-form>
				<el-row>
					<el-col :span="16">
						<el-button type="text" v-if="!voteDetail.anonymous" @click="getDiglogData">参与投票成员</el-button>
						<el-button type="text" @click="deleteVote(voteDetail.themeId)">删除投票</el-button>
					</el-col>
					<el-col :span="8">截止时间:{{voteDetail.endTime}}</el-col>
				</el-row>
				<div class="buttonset">
					<el-button size="small" @click="votedialog.show=false">关闭</el-button>
				</div>
			</div>
		</el-dialog>

		<el-dialog v-else :title="voteDetail.content" :visible.sync="votedialog.show" :close-on-click-modal='false'
		 :close-on-press-escape='false' :modal-append-to-body="false" width="40%">
			<div class="form">
				<el-row>
					<el-col :span="20">{{voteDetail.isSelectOne?"单选":"多选"}}</el-col>
					<el-col :span="4">共{{voteNumber}}票</el-col>
				</el-row>
				<el-form class="el_form">
					<el-form-item style="width: 95%;" class="el_form_item" v-for="(o, index) in voteDetail.voteOptionList" :key="index"
					 :offset="index > 0 ? voteDetail.voteOptionList.length : 0" @click="">
						<el-row>
							<el-col :span="15">
								{{o.content}}
							</el-col>
							<el-col :span="6">
								<el-progress style="margin-top: 10px" :text-inside="true" :stroke-width="20" :percentage="Math.round(o.userList.length/voteNumber*100)"></el-progress>
							</el-col>
							<el-col :span="3">{{o.userList.length}}票</el-col>
						</el-row>
					</el-form-item>

				</el-form>
				<el-row class="el_rowSet">
					<el-col :span="20" style="padding-top: 5px;">已截止
						<el-button type="text" @click="deleteVote(voteDetail.themeId)">删除投票</el-button>
					</el-col>
					<el-col :span="4">
						<el-button type="text" v-if="!voteDetail.anonymous" @click="getDiglogData">参与投票成员</el-button>
					</el-col>
				</el-row>
				<div class="buttonset">
					<el-button size="small" @click="votedialog.show=false">关闭</el-button>
				</div>
			</div>
		</el-dialog>

		<el-dialog title="已投票人员" :visible.sync="dialogVisible" width="30%" :close-on-click-modal='false'
		 :close-on-press-escape='false' :modal-append-to-body="false">
			<el-form inline>
				<el-form-item v-for="(o, index) in nameList" :key="index">{{o}}</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" @click="dialogVisible = false">关 闭</el-button>
			</span>
		</el-dialog>

	</div>
</template>

<script>
	import qs from 'qs'
	export default {
		name: "voteDialog",
		props: {
			votedialog: Object,
			voteDetail: Object,
			voteNumber: Number
		},
		inject: ['reload'],
		data() {
			return {
				checkedValue: 0,
				checkedList: [],
				radio: 1,
				percent: 0,
				dialogVisible: false,
				nameList: []

			};
		},
		methods: {
			getDiglogData() {
				this.dialogVisible = true;
				var arr = [];
				// console.log(this.voteDetail)
				if (!(JSON.stringify(this.voteDetail) == "{}")) { //取得已投票数
					var m = this.voteDetail.voteOptionList;
					for (var i = 0; i < m.length; i++) {
						for (var j = 0; j < m[i].userList.length; j++) {
							arr.push(m[i].userList[j].realName);
						}
					}
				}
				this.nameList = this.unique(arr);
				// console.log(this.nameList)
			},
			unique(arr) {
				// 遍历arr，把元素分别放入tmp数组(不存在才放)
				var tmp = [];
				for (var i in arr) {
					//该元素在tmp内部不存在才允许追加
					if (tmp.indexOf(arr[i]) == -1) {
						tmp.push(arr[i]);
					}
				}
				return tmp;
			},

			//用户执行投票
			onSubmit() {

				var data = {
					themeId: this.voteDetail.themeId,
					userId: localStorage.getItem("userId"),
				};
				var option;
				if (this.voteDetail.isSelectOne == 1) {
					if (this.checkedValue != "") {
						let key = 'optionId1';
						let value = this.checkedValue;
						data[key] = value;
					} else {
						return;
					}
				} else {
					//判断是否有选择，有选择则将数组的数据逐个放进对象中
					if (this.checkedList.length) {
						for (var i = 0; i < this.checkedList.length; i++) {
							let key = 'optionId' + (i + 1);
							let value = this.checkedList[i];
							data[key] = value;
						}
					} else {
						return;
					}
				}

				// console.log(JSON.stringify(data) + "数据");
				this.$axios({
						method: 'POST',
						url: '/vote/saveOptionUser',
						headers: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						data: qs.stringify(data)
					})
					.then(res => {
						this.$message({
							message: "投票成功",
							type: "success"
						});
						this.votedialog.show = false;
						this.$emit("update");
					});
			},
			deleteVote(themeId) {
				//删除投票主题
				// console.log(themeId);
				this.$confirm('确认删除当前投票主题, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$axios({
						method: 'get',
						url: '/vote/deleteByThemeId',
						params: {
							themeId: themeId
						},
					}).then(res => {
						this.$message({
							message: "删除投票主题成功！",
							type: "success"
						});
						this.votedialog.show = false;
					});
					this.reload();

				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});

			}
		}
	};
</script>
<style scoped>
	.buttonset {
		margin-top: 10px;
		margin-left: 40%;
	}

	.el_radiogroup {
		width: 97%;
	}

	.el_form {
		margin: 10px 0px;
	}

	.el_form_item {
		border-bottom: #FFFFFF 1px solid;
		margin-bottom: 0;
		padding-left: 20px;
		background-color: rgb(246, 246, 246);
	}

	.el_radio {
		width: 500px;
	}

	.bor_row {
		padding: 15px 0 5px 10px;
		width: 97%;
		margin-bottom: 10px;
		border-bottom: 1px solid rgb(246, 246, 246);
	}

	.el_rowSet {
		width: 97%;
		border-bottom: 1px solid rgb(246, 246, 246);
	}
</style>



