import Vue from 'vue'
import Router from 'vue-router'
import Index from '../views/Index.vue'
import Register from '../views/Register'
import Nofind from '../views/404'
import Login from '../views/Login'
import Home from '../views/Home'
import InfoShow from '../views/InfoShow'
import FoundList from '../views/FoundList'
import Votes from '../views/Votes'
import Whole from '../views/Whole'
import Visitor from '../views/Visitor'
import Department from '../views/department'
import PersonFile from '../views/personFile'
import PublicFile from '../views/publicFile'

Vue.use(Router)

const router = new Router({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
	  { path: '*', name: '/404', component: Nofind },
	  {path: '/', redirect: '/index' },
	  { path: '/Register', name: 'register', component: Register },
	  { path: '/login', name: 'login', component: Login },
	  {
	    path: '/index',
	    name: 'index',
	    component: Index,
	    children: [
	      { path: '', component: Home },
	      { path: '/home', name: 'home', component: Home },
	      { path: '/infoshow', name: 'infoshow', component: InfoShow },
	      { path: '/foundlist', name: 'foundlist', component: FoundList },
	      { path: '/votes', name: 'votes', component: Votes},
	      { path: '/whole', name: 'whole', component: Whole},
	      { path: '/visitor', name: 'visitor', component: Visitor},
		  {path: '/Votes', name: 'votes', component: Votes},
		  {path: '/department',name:'department',component:Department},
		  {path: '/personFile', name: 'personFile', component: PersonFile},
		   {path: '/publicFile', name: 'publicFile', component: PublicFile},
	    ] ,
		},
	]



// {
//   path: '/about',
//   name: 'about',
//   // route level code-splitting
//   // this generates a separate chunk (about.[hash].js) for this route
//   // which is lazy-loaded when the route is visited.
//   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
// }

})




export default router;



