//添加路由守卫
router.beforeEach((to, from, next) => {
	const isLogin = localStorage.token ? true : false;
	if (to.path == "/login" || to.path == "/Register") {
		next();
		console.log(to.path);
	} else {
		isLogin ? next() : next("/login");
	}
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')



